import React from 'react';
import { baseImageUrl } from '../../Constants/UrlConstant';
import st from './BlogCard.module.css';

export default function BlogCard(props) {
    let {big_image, name, create_date, short_desc, user} = props.data;
    return (
        <div className={st.wrap_blog_card}>
            <div className={st.wrap_img}>
                <img src={`${baseImageUrl}/blogs/${big_image}`} alt={big_image} />
            </div>
            <div className={st.main_title}>
                <div className={st.title}>{name}</div>
                <div className={st.info}>
                    <div className={st.item}>
                        <span><i className="fa-solid fa-calendar-days"></i></span>
                        <span>{create_date}</span>
                    </div>
                    <div className={st.item}>
                        <span><i className="fa-solid fa-user"></i></span>
                        <span>{user.name}</span>
                    </div>
                </div>
            </div>
            <div className={st.sub_title}>
                <div className={st.desc}>{short_desc}</div>
            </div>
        </div>
    )
}
