import React, { useEffect, useState } from 'react';
import st from './InputTotal.module.css';

export default function InputTotal(props) {

    let [total, setTotal] = useState(props.totalProduct);

    useEffect(function(){
        props.total(total);
    }, [total]);

    return (
        <div className={st.wrap_total_product}>
            <button
                onClick={()=>{
                    let newTotal = total - 1;
                    if(newTotal < 1){
                        newTotal = 1;
                    }
                    setTotal(parseInt(newTotal));
                }}
            >-</button>
            <input
                onChange = {
                    (e)=> {
                        let newTotal = 1;
                        if(
                            e.target.value < 1 ||
                            isNaN(e.target.value)
                        ){
                            newTotal = total;
                        }else{
                            newTotal = e.target.value;
                        }
                        setTotal(parseInt(newTotal));
                    }
                }
                type="text"
                value={total}
            />
            <button
                onClick={()=>{
                    setTotal(parseInt(total) + 1);
                }}
            >+</button>
        </div>
    )
}
