import React from 'react';
import { baseImageUrl } from '../../Constants/UrlConstant';
import st from './ProductCard.module.css';

export default function ProductCard(props) {
    let {image_1, image_2, name, price_sale, price_root} = props?.data;
    return (
        <div className={st.product_card}>
            <div className={st.top}>
                <img src={`${baseImageUrl}/products/${image_1}`} alt={image_1} />
                <div className={st.view_button}>Xem sản phẩm</div>
            </div>
            <div className={st.bottom}>
                <div className={st.name}>{name}</div>
                <div className={st.price}>
                    <span>{price_sale.toLocaleString()}đ</span>
                    <del>{price_root.toLocaleString()}đ</del>
                </div>
            </div>
        </div>
    )
}
