import React from 'react';
import BreadCrumb from '../../Components/BreadCrumb/BreadCrumb';
import Select from 'react-select';
import st from './Checkout.module.css';
import axios from 'axios';
import { useAlert, positions } from 'react-alert';
import { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { baseApiUrl, baseImageUrl, baseUrl } from '../../Constants/UrlConstant';
import { shipPrice } from '../../Constants/ShipConstant';
import { Helmet } from "react-helmet";

function Checkout(props) {
    
    let navigate = useNavigate();
    let alert = useAlert();
    let [provinces, setProvinces] = useState([]);
    let [districts, setDistricts] = useState([]);
    let [towns, setTowns] = useState([]);
    let [filterLocation, setFilterLocation] = useState({
        provinceId: null,
        districtId: null,
        townId: null
    });
    let [checkoutData, setCheckoutData] = useState({
        cart: props.cart.cart
    });
    let [errors, setErrors] = useState();
    let [disabledButton, setDisabledButton] = useState(false);

    function handleSetCheckoutData(name, value){
        let newCheckoutData = {...checkoutData};
        newCheckoutData[name] = value;
        setCheckoutData(newCheckoutData);
    }

    function createBill(){
        axios.post(`${baseApiUrl}/create-bill`, checkoutData)
        .then(function(res){
            let data = res.data;
            console.log(data);
            if(data.status === false){
                setErrors(data.message);
                setDisabledButton(false);
                window.scrollTo(0, 0);
            }else{
                window.scrollTo(0, 0);
                setDisabledButton(true);
                alert.show('Đặt hàng thành công !', {
                    type: 'success',
                    timeout: 3000,
                    position: positions.TOP_RIGHT,
                });
                setTimeout(function(){
                    props.destroyCart();
                    navigate(`/`);
                }, 4000);
            }
        });
    }

    useEffect(function () {
        if(props.cart.cart.length === 0){
            navigate('/cart.html');
        }
        axios.get(`${baseApiUrl}/provinces`)
            .then(function (res) {
                let provinces = res.data.data.map(function (value, index) {
                    return {
                        value: value.matp,
                        label: value.name
                    }
                });
                setProvinces(provinces);
            })
    }, []);

    useEffect(function () {
        axios.get(`${baseApiUrl}/districts?matp=${filterLocation.provinceId}`)
            .then(function (res) {
                let districts = res.data.data.map(function (value, index) {
                    return {
                        value: value.maqh,
                        label: value.name
                    }
                });
                setDistricts(districts);
            })
    }, [filterLocation.provinceId]);

    useEffect(function () {
        axios.get(`${baseApiUrl}/towns?maqh=${filterLocation.districtId}`)
            .then(function (res) {
                let towns = res.data.data.map(function (value, index) {
                    return {
                        value: value.xaid,
                        label: value.name
                    }
                });
                setTowns(towns);
            })
    }, [filterLocation.districtId]);

    function renderProducts() {
        return props.cart?.cart.map(function (value, index) {
            return (
                <Link to={`/product/${value.url}.html`}>
                    <div className={st.wrap_product_item}>
                        <div className={st.main_img}>
                            <div className={st.wrap_img}>
                                <img src={`${baseImageUrl}/products/${value.image}`} alt={value.image} />
                                <span>{value.total}</span>
                            </div>
                        </div>
                        <div className={st.info_product_item}>
                            <div className="left">
                                <div className={st.product_name}>{value.name}</div>
                                <div className={st.detail}>Size {value.sizeName}</div>
                            </div>
                            <div className={st.right_price_item}>
                                {value.price.toLocaleString()}đ
                            </div>
                        </div>
                    </div>
                </Link>
            );
        })
    }

    function totalOrder(){
        return props.cart?.cart.reduce(function(count, item){
            return count += item.total * item.price;
        }, 0);
    }

    function renderErrorItem(err){
        return err?.map(function(value, index){
            return <div key={index} className={st.err_msg}>{value}</div>
        });
    }

    return (
        <div className={st.order_page}>
            <Helmet>
                <title>Fire Sport Shop - Đặt hàng</title>
                <meta property="og:title" content="Fire Sport Shop - Đặt hàng" />
                <meta name="description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
                <meta property="og:description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
                <meta
                    property="og:image"
                    content=""
                />
                <meta property="og:image:alt"
                    content=""
                />
                <meta property="og:site_name" content={baseUrl} />
                <meta property="og:url" content={`${baseUrl}/checkout.html`} />
            </Helmet>
            <BreadCrumb></BreadCrumb>
            <div className={`container ${st.wrap_container}`}>
                <div className="row">
                    <div className={`col-lg-7 ${st.col_left}`}>
                        <h3 className={st.title_left}>Thông tin nhận hàng</h3>
                        <div className={`${st.wrap_input} ${errors?.name ? st.wrap_input_error : ""}`}>
                            <label htmlFor="name">Họ tên <span>*</span></label>
                            <input
                                id="name"
                                name='name'
                                className={st.input}
                                type="text"
                                placeholder='Họ và tên'
                                onChange={(e)=>{
                                    handleSetCheckoutData(e.target.name, e.target.value);
                                }}
                            />
                            {renderErrorItem(errors?.name)}
                        </div>
                        <div className={`${st.wrap_input} ${errors?.phone ? st.wrap_input_error : ""}`}>
                            <label htmlFor="phone">Số điện thoại <span>*</span></label>
                            <input
                                id='phone'
                                name='phone'
                                className={st.input}
                                type="text"
                                placeholder='Số điện thoại'
                                onChange={(e)=>{
                                    handleSetCheckoutData(e.target.name, e.target.value);
                                }}
                            />
                            {renderErrorItem(errors?.phone)}
                        </div>
                        <div className={`${st.wrap_input} ${errors?.address ? st.wrap_input_error : ""}`}>
                            <label htmlFor="address">Địa chỉ giao <span>*</span></label>
                            <input
                                id='address'
                                name='address'
                                className={st.input}
                                type="text"
                                placeholder='Địa chỉ'
                                onChange={(e)=>{
                                    handleSetCheckoutData(e.target.name, e.target.value);
                                }}
                            />
                            {renderErrorItem(errors?.address)}
                        </div>
                        <div className={st.wrap_input}>
                            <label htmlFor="">Tỉnh/Thành <span>*</span></label>
                            <Select
                                height={50}
                                options={provinces}
                                onChange={(value) => {
                                    let newFilterLocation = { ...filterLocation };
                                    newFilterLocation.provinceId = value.value;
                                    setFilterLocation(newFilterLocation);
                                }}
                            ></Select>
                        </div>
                        <div className={st.wrap_input}>
                            <label htmlFor="">Quận/Huyện <span>*</span></label>
                            <Select
                                isDisabled={filterLocation.provinceId === null ? true : false}
                                height={50}
                                options={districts}
                                onChange={(value) => {
                                    let newFilterLocation = { ...filterLocation };
                                    newFilterLocation.districtId = value.value;
                                    setFilterLocation(newFilterLocation);
                                }}
                            ></Select>
                        </div>
                        <div className={`${st.wrap_input} ${errors?.town_id ? st.wrap_input_error : ""}`}>
                            <label htmlFor="town">Phường/Thị Trấn <span>*</span></label>
                            <Select
                                id='town'
                                height={50}
                                options={towns}
                                isDisabled={filterLocation.districtId === null ? true : false}
                                onChange={(value)=>{
                                    handleSetCheckoutData('town_id', value.value);
                                }}
                            ></Select>
                            {renderErrorItem(errors?.town_id)}
                        </div>
                        <div className={`${st.wrap_input} ${errors?.note ? st.wrap_input_error : ""}`}>
                            <label htmlFor="note">Ghi chú</label>
                            <textarea
                                id='note'
                                name='note'
                                className={st.input_text_area}
                                placeholder='Ghi chú'
                                onChange={(e)=>{
                                    handleSetCheckoutData(e.target.name, e.target.value);
                                }}
                            ></textarea>
                            {renderErrorItem(errors?.note)}
                        </div>
                    </div>
                    <div className={`col-lg-5 ${st.col_right}`}>
                        <h3 className={st.title_left}>Đơn hàng</h3>
                        <div className={st.wrap_items}>
                            {renderProducts()}
                        </div>
                        <div className={st.wrap_item}>
                            <h3 className={st.title_left}>Vận chuyển</h3>
                            <div className={st.trans}>
                                <div className='d-flex align-items-center'><span></span><div>GIAO HÀNG</div></div>
                                <div>{shipPrice.toLocaleString()}đ</div>
                            </div>
                        </div>
                        <div className={st.wrap_item}>
                            <h3 className={st.title_left}>Thanh toán</h3>
                            <div className={st.trans}>
                                <div className='d-flex align-items-center'><span></span><div>Thanh toán khi giao hàng (COD)</div></div>
                                <div><i class="fa-solid fa-money-bill"></i></div>
                            </div>
                        </div>
                        <div className={st.pay_total_info}>
                            <div className={st.item_pay_info}>
                                <span>Tạm tính</span>
                                <span>{totalOrder().toLocaleString()}đ</span>
                            </div>
                            <div className={st.item_pay_info}>
                                <span>Phí vận chuyển</span>
                                <span>{shipPrice.toLocaleString()}đ</span>
                            </div>
                            <hr />
                            <div className={st.wrap_total_all}>
                                <span>Tổng cộng</span>
                                <span>{(totalOrder() + shipPrice).toLocaleString()}đ</span>
                            </div>
                        </div>
                        <hr />
                        <div className={st.wrap_button_pay}>
                            <div className={st.button_back}>
                                <i class="fa-solid fa-angle-left"></i>
                                <div><Link to="/cart.html">Quay về giỏ hàng</Link></div>
                            </div>
                            <button
                                disabled={disabledButton ? true : false}
                                className={`${st.buy_now} ${disabledButton ? st.buy_now_disabled : ""}`}
                                onClick={()=>{
                                    setDisabledButton(true);
                                    createBill();
                                }}
                            >ĐẶT HÀNG NGAY</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

function mapStateToProps(state) {
    return state;
}

function mapDispatchToProps(dispatch){
    return {
        destroyCart: function () {
            let action = {
                type: "DESTROY_CART"
            }
            dispatch(action);
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Checkout)