import React from 'react';
import BreadCrumb from '../../Components/BreadCrumb/BreadCrumb';
import BlogCard from '../../Components/BlogCard/BlogCard';
import st from './BlogCategory.module.css';
import { Pagination } from '@mui/material';
import BlogCategoryCards from '../../Components/BlogCategoryCards/BlogCategoryCards';
import { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { baseApiUrl } from '../../Constants/UrlConstant';
import { blogCategory } from '../../Constants/PaginateConstant';
import { Link } from 'react-router-dom';

export default function BlogCategory() {

    let [blogs, setBlogs] = useState();
    let [page, setPage] = useState(1);

    useEffect(function(){
        axios.get(`${baseApiUrl}/get-blogs?limit=${blogCategory}&order_by=id|desc&page=${page}`)
        .then(function(res){
           setBlogs(res.data);
        });
    }, [page]);

    function renderBlogs() {
        return blogs?.data.map(function (value, index) {
            return <Link key={index} to={`/blog/${value.url}.html`}><BlogCard data={value}></BlogCard></Link>;
        });
    }

    return (
        <div>
            <BreadCrumb></BreadCrumb>
            <div className={`container ${st.container_all}`}>
                <div className="row">
                    <div className={`col-lg-4 ${st.col_left}`}>
                        <BlogCategoryCards
                            title="Tin mới nhất"
                            url={`${baseApiUrl}/get-blogs?order_by=id|desc`}
                        />
                        <BlogCategoryCards
                            title="Tin hot nhất"
                            url={`${baseApiUrl}/get-blogs?order_by=id|desc&special=true`}
                        />
                    </div>
                    <div className={`col-lg-8 ${st.col_right}`}>
                        <div className="text-center">
                            <h2 className={`pb-2`}>Tin tức</h2>
                        </div>
                        <div className={st.wrap_blog_card}>
                            {renderBlogs()}
                        </div>
                        <div className={st.wrap_paginate}>
                            <Pagination
                                page={page}
                                count={Math.ceil(blogs?.total / blogCategory)}
                                onChange={(event, value)=>{
                                    window.scrollTo(0, 0);
                                    setPage(value);
                                }}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
