import React from 'react';
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import ProductCategory from "./Pages/ProductCategory/ProductCategory";
import Home from "./Pages/Home/Home";
import Navbar from './Components/Navbar/Navbar';
import Footer from './Components/Footer/Footer';
import ProductDetail from './Pages/ProductDetail/ProductDetail';
import Cart from './Pages/Cart/Cart';
import Checkout from './Pages/Checkout/Checkout';
import BlogCategory from './Pages/BlogCategory/BlogCategory';
import BlogDetail from './Pages/BlogDetail/BlogDetail';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Navbar></Navbar>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/product-category.html" element={<ProductCategory />} />
          <Route path="/product-category/category/:category_url.html" element={<ProductCategory />} />
          <Route path="/product-category/keyword/:keyword.html" element={<ProductCategory />} />
          <Route path="/product/:url.html" element={<ProductDetail />} />
          <Route path="/cart.html" element={<Cart />} />
          <Route path="/checkout.html" element={<Checkout />} />
          <Route path="/blog-category.html" element={<BlogCategory />} />
          <Route path="/blog/:url.html" element={<BlogDetail />} />
        </Routes>
        <Footer></Footer>
      </BrowserRouter>
    </div>
  );
}

export default App;