import React from 'react';
import { baseImageUrl } from '../../Constants/UrlConstant';
import st from './ProductCardSearch.module.css';

export default function ProductCardSearch(props) {
    return (
        <div className={st.product_card}>
            <div className={st.left}>
                <img
                    className={st.img}
                    src={`${baseImageUrl}/products/${props.value.image_1}`}
                    alt={props.value.image_1}
                />
            </div>
            <div className={st.right}>
                <b>{props.value.name}</b>
                <div className={st.price}><b>150.000đ</b><del>200.000đ</del></div>
            </div>
        </div>
    )
}
