import React from 'react';
import st from './Category.module.css';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { baseApiUrl, baseImageUrl } from '../../Constants/UrlConstant';
import { useNavigate } from 'react-router-dom';

export default function Category(props) {

    let navigate = useNavigate();
    let [categories, setCategories] = useState();

    useEffect(function(){
        axios.get(`${baseApiUrl}/all-category?limit=3`)
        .then(function(res){
            setCategories(res.data.data);
        });
    }, []);

    return (
        <div className="wrap_category">
            <div className='row'>
                <div
                    className="col-lg-6"
                    onClick={()=>{
                        window.scrollTo(0, 0);
                        navigate(`/product-category/category/${categories ? categories[0].url : ""}.html`)
                    }}
                >
                    <div className={st.category_item_left}>
                        <img src={`${baseImageUrl}/productCategories/${categories ? categories[0].big_image : ""}`} alt="category-1" />
                    </div>
                </div>
                <div className="col-lg-6">
                    <div
                        className={st.category_item_right}
                        onClick={()=>{
                            window.scrollTo(0, 0);
                            navigate(`/product-category/category/${categories ? categories[1].url : ""}.html`)
                        }}
                    >
                        <img src={`${baseImageUrl}/productCategories/${categories ? categories[1].big_image : ""}`} alt="category-2" />
                    </div>
                    <div
                        className={st.category_item_right}
                        onClick={()=>{
                            window.scrollTo(0, 0);
                            navigate(`/product-category/category/${categories ? categories[2].url : ""}.html`)
                        }}
                    >
                        <img src={`${baseImageUrl}/productCategories/${categories ? categories[2].big_image : ""}`} alt="category-3" />
                    </div>
                </div>
            </div>
        </div>
    )
}
