import React, { useState } from 'react'
import MobileProductCart from '../MobileProductCart/MobileProductCart'
import ProductCardSearch from '../ProductCardSearch/ProductCardSearch'
import st from './Navbar.module.css'
import { Link, useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import axios from 'axios';
import {connect} from 'react-redux';
import { baseApiUrl } from '../../Constants/UrlConstant';
import { productMenuSearchMobile } from '../../Constants/PaginateConstant';

function Navbar(props) {

    let navigate = useNavigate();
    let [openMenu, setOpenMenu] = useState(false);
    let [openMainMenu, setOpenMainMenu] = useState(false);
    let [openSearchBox, setOpenSearchBox] = useState(false);
    let [openMobileCart, setOpenMobileCart] = useState(false);
    let [keyword, setKeyword] = useState(""); 
    let [categories, setCategory] = useState();
    let [productSearch, setProductSearch] = useState([]);

    useEffect(function () {
        axios.get(`${baseApiUrl}/all-category`)
            .then(function (res) {
                setCategory(res.data.data);
            });
    }, []);

    function getProductSearch(){
        if(keyword !== ""){
            keyword = `?keyword=${keyword}`;
        }
        axios.get(`${baseApiUrl}/get-products${keyword}`)
        .then(function(res){
            setProductSearch(res.data.data.data);
        });
    }

    function renderProductSearch(){
        return productSearch?.map(function(value, index){
            return (
                <div
                    onClick={()=>{
                        closeAllMenu();
                        window.scrollTo(0, 0);
                        navigate(`/product/${value.url}.html`);
                    }}
                >
                    <ProductCardSearch
                        key={index}
                        value={value}
                    />
                </div>
            )
        });
    }

    function renderCategory() {
        return categories?.map(function (value, index) {
            return (
                <li key={index} className={st.menu_bottom_item}>
                    <Link to={`/product-category/category/${value.url}.html`}>
                        <div>{value.name}</div>
                    </Link>
                </li>
            );
        });
    }

    function countTotalProduct(){
        return props.cart.cart.reduce(function(num, item){
            return num+=1;
        }, 0);
    }

    function searchProduct(e){
        e.preventDefault();
        window.scrollTo(0, 0);
        if(keyword !== ""){
            navigate(`/product-category/keyword/${keyword}.html`);
        }else{
            navigate(`/product-category/keyword/all.html`);
        }
    }

    function closeAllMenu(){
        setOpenMenu(false);
        setOpenMainMenu(false);
        setOpenSearchBox(false);
        setOpenMobileCart(false);
    }

    function renderProductCart(){
        return props.cart?.cart.map(function(value, index){
            return (
                <MobileProductCart
                    total={(total)=>{
                        props.editCartItem({
                            id: value.id,
                            color: value.color,
                            size: value.size,
                            total: total
                        });
                    }}
                    totalProduct={value.total}
                    key={index}
                    value={value}
                />
            )
        });
    }

    return (
        <nav className={st.nav_header}>
            <div className="container">
                <div className={st.header_main}>
                    <div className={st.wrap_logo}>
                        <Link to="/">
                            <img src="/img/logo.png" alt="logo" />
                        </Link>
                    </div>
                    <div className={st.menu_items}>
                        <ul>
                            <li className={st.menu_item}>
                                <Link to="/">
                                    <div className={st.menu_item_top}>
                                        <div>Trang chủ</div>
                                    </div>
                                </Link>
                            </li>
                            <li className={st.menu_item}>
                                <Link to="/product-category.html">
                                    <div className={st.menu_item_top}>
                                        <div>Sản phẩm</div>
                                        <div className={st.menu_angle_down}><i className="fa-solid fa-chevron-down"></i></div>
                                    </div>
                                </Link>
                                <div className={st.menu_item_bottom}>
                                    <ul>
                                        {renderCategory()}
                                    </ul>
                                </div>
                            </li>
                            <li className={st.menu_item}>
                                <Link to="/product-detail">
                                    <div className={st.menu_item_top}>
                                        <div>Sale All</div>
                                    </div>
                                </Link>
                            </li>
                            <li className={st.menu_item}>
                                <Link to="/blog-detail">
                                    <div className={st.menu_item_top}>
                                        <div>Membership</div>
                                    </div>
                                </Link>
                            </li>
                            <li className={st.menu_item}>
                                <Link to="/blog-category.html">
                                    <div className={st.menu_item_top}>
                                        <div>Tin tức</div>
                                    </div>
                                </Link>
                            </li>
                            <li className={st.menu_item}>
                                <Link to="/blog/gioi-thieu.html">
                                    <div className={st.menu_item_top}>
                                        <div>Giới thiệu</div>
                                    </div>
                                </Link>
                            </li>
                        </ul>
                    </div>
                    <div className={st.wrap_head_control}>
                        <div className={st.search_box}>
                            <div className={st.search_box_icon}>
                                <i className="fa-solid fa-magnifying-glass"></i>
                            </div>
                            <form onSubmit={(e)=>{
                                searchProduct(e);
                            }} className={st.search_box_input}>
                                <div className={st.wrap_search_input}>
                                    <input onChange={(e)=>{
                                        setKeyword(e.target.value);
                                    }} type="text" />
                                    <span
                                        onClick={(e)=>{
                                            searchProduct(e);
                                        }}
                                    >
                                        <i className="fa-solid fa-magnifying-glass"></i>
                                    </span>
                                </div>
                            </form>
                        </div>
                        <div className={st.search_box}>
                            <div className={st.search_box_icon}>
                                <i className="fa-solid fa-user"></i>
                            </div>
                            <div className={st.menu_user}>
                                <ul>
                                    <li>
                                        <div>Đăng nhập</div>
                                    </li>
                                    <li>
                                        <div>Đăng ký</div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className={st.card_header}>
                            <div className={st.card_header_left}>
                                <i className="fa-solid fa-cart-shopping"></i>
                            </div>
                            <Link to="/cart.html">
                                <div className={st.card_header_right}>
                                    <div>Giỏ hàng</div>
                                    <div>({countTotalProduct()} sản phẩm)</div>
                                </div>
                            </Link>
                        </div>
                    </div>
                    <div className={st.mobile_header_menu}>
                        <div
                            className={st.mobile_search_icon}
                            onClick={() => {
                                setOpenMenu(true);
                                setOpenMobileCart(true);
                            }}
                        >
                            <i className="fa-solid fa-cart-shopping"></i>
                            <span className={st.cart_total_num}>{countTotalProduct()}</span>
                        </div>
                        <div
                            className={st.mobile_search_icon}
                            onClick={() => {
                                setOpenMenu(true);
                                setOpenSearchBox(true);
                            }}
                        >
                            <i className="fa-solid fa-magnifying-glass"></i>
                        </div>
                        <div
                            className={st.mobile_search_icon}
                            onClick={() => {
                                setOpenMenu(true);
                                setOpenMainMenu(true);
                            }}
                        >
                            <i className="fa-solid fa-bars"></i>
                        </div>
                        <div className={`${st.mobile_menu} ${openMenu === true ? st.show_menu : st.hide_menu}`}>
                            <div
                                onClick={() => {
                                    setOpenMenu(false);
                                    setOpenMainMenu(false);
                                    setOpenSearchBox(false);
                                    setOpenMobileCart(false);
                                }}
                                className={st.background_close_menu}
                            ></div>
                            <div className={`${st.main_menu} ${openMenu ? st.show_main_menu : ''}`}>
                                <div className={st.wrap_mobile_menu_logo}>
                                    <img src="/img/logo.png" alt="logo" />
                                </div>
                                <ul className={`menu ${st.mobile_menu_ul} ${openMainMenu ? '' : 'd-none'}`}>
                                    <li className={st.mobile_menu_item}>
                                        <Link
                                            to="/"
                                            onClick={()=>{closeAllMenu()}}
                                        ><div>Trang chủ</div></Link>
                                    </li>
                                    <li className={st.mobile_menu_item}>
                                        <Link
                                            onClick={()=>{closeAllMenu()}}
                                            to="/product-category.html"
                                        >
                                            <div>Sản phẩm</div>
                                        </Link>
                                    </li>
                                    <li className={st.mobile_menu_item}><div>Sale all</div></li>
                                    <li className={st.mobile_menu_item}><div>Membership</div></li>
                                    <li className={st.mobile_menu_item}>
                                        <Link
                                            onClick={()=>{closeAllMenu()}}
                                            to="/blog-category.html"
                                        >
                                            <div>Tin tức</div>
                                        </Link>
                                    </li>
                                    <li className={st.mobile_menu_item}><div>Giới thiệu</div></li>
                                    <li className={st.mobile_menu_item}><div>Liên hệ</div></li>
                                </ul>
                                <div className={`search ${openSearchBox ? '' : 'd-none'}`}>
                                    <form
                                        onSubmit={(e)=>{
                                            e.preventDefault();
                                            getProductSearch();
                                        }}
                                        className={st.form_search_mobile}
                                        action=""
                                    >
                                        <input
                                            placeholder='Tìm kiếm...'
                                            type="text"
                                            value={keyword}
                                            onChange={(e)=>{
                                                setKeyword(e.target.value);
                                            }}
                                        />
                                        <button
                                            onClick={(e)=>{
                                                e.preventDefault();
                                                getProductSearch();
                                            }}
                                        >Tìm kiếm</button>
                                    </form>
                                    <img className={`${st.empty_box} d-none`} src="/img/emptybox.png" alt="empty_box" />
                                    <div className={st.wrap_search_product}>
                                        {renderProductSearch()}
                                        <Link to={`/product-category.html`}><div>Xem toàn bộ</div></Link>
                                    </div>
                                </div>
                                <div className={`cart ${openMobileCart ? '' : 'd-none'}`}>
                                    <b>Giỏ hàng</b>
                                    <div className={`${st.list_product_cart}`}>
                                        {renderProductCart()}
                                    </div>
                                    {
                                        props.cart?.cart.length === 0
                                            ? <div>Không có sản phẩm nào</div>
                                            : ""
                                    }
                                    <Link
                                        className={props.cart?.cart.length === 0 ? "d-none" : ""}
                                        to={`/checkout.html`}
                                        onClick={()=>{
                                            closeAllMenu();
                                        }}
                                    >
                                        <button className={st.create_bill_btn}>Tiến hành đặt hàng</button>
                                    </Link>
                                </div>
                                <ul className={st.mobile_icons}>
                                    <li>
                                        <div><i className="fa-brands fa-facebook-square"></i></div>
                                    </li>
                                    <li>
                                        <div><i className="fa-brands fa-instagram-square"></i></div>
                                    </li>
                                    <li>
                                        <div><i className="fa-brands fa-youtube"></i></div>
                                    </li>
                                    <li>
                                        <div><i className="fa-brands fa-google"></i></div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </nav>
    )
}

function mapStateToProps(state){
    return state;
}

function mapDispatchToProps(dispatch) {
    return {
        editCartItem: function (product) {
            let action = {
                type: "EDIT_CART",
                data: {
                    id: product.id,
                    total: product.total,
                    color: product.color,
                    size: product.size
                }
            }
            dispatch(action);
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);