import React, { useState } from 'react';
import st from './BlogCategoryCards.module.css';
import axios from 'axios';
import { useEffect } from 'react';
import { baseImageUrl } from '../../Constants/UrlConstant';
import { useNavigate } from 'react-router-dom';

export default function BlogCategoryCards({ title, url }) {

    let navigate = useNavigate();
    let [blogs, setBlogs] = useState([]);

    useEffect(function () {
        axios.get(url).then(function (res) {
            setBlogs(res.data.data);
        });
    }, []);

    function renderBlogs() {
        return blogs?.map(function (value, index) {
            return (
                <div
                    key={index}
                    className={st.card_category}
                    onClick={()=>{
                        window.scrollTo(0, 0);
                        navigate(`/blog/${value.url}.html`)
                    }}
                >
                    <div className={st.wrap_img_category}>
                        <img src={`${baseImageUrl}/blogs/${value.big_image}`} alt={value.big_image} />
                    </div>
                    <div className={st.wrap_content}>
                        <div className="name">{value.name}</div>
                    </div>
                </div>
            );
        });
    }

    return (
        <div className="wrap_category mb-3">
            <div className={st.title_category}>{title}</div>
            <div className="wrap_cards">
                {renderBlogs()}
            </div>
        </div>
    )
}
