import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Pagination, Navigation, Lazy } from "swiper";
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import 'swiper/css/lazy';
import st from './Slider.module.css';
import { useEffect } from 'react';
import axios from 'axios';
import { baseApiUrl, baseImageUrl } from '../../Constants/UrlConstant';
import { useState } from 'react';

export default function Slider({sliders}) {

    function renderSlider() {
        return sliders.map(function (value, index) {
            return (
                <SwiperSlide key={index} className={st.slider_wrap_item}>
                    <img src={`${baseImageUrl}/sliders/${value.big_image}`} alt={index} />
                </SwiperSlide>
            );
        });
    }

    return (
        <div className={st.wrap_slider}>
            <Swiper
                style={{
                    "--swiper-pagination-bullet-size": '15px',
                    "--swiper-pagination-bullet-inactive-color": "#ffffff",
                    "--swiper-pagination-bullet-inactive-opacity": "0.6",
                    "--swiper-pagination-color": "#404040",
                }}
                className='slider'
                spaceBetween={0}
                slidesPerView={1}
                loop={true}
                autoplay={{
                    delay: 10000,
                    disableOnInteraction: false,
                }}
                pagination={{
                    clickable: true,
                }}
                lazy={true}
                modules={[Autoplay, Pagination, Navigation, Lazy]}
            >

                {renderSlider()}
            </Swiper>
        </div>
    )
}
