const initState = {
    cart: []
};

const BaiTapGioHangReducer = (state = initState, action) => {
    switch (action.type) {
        case 'ADD_TO_CART': {
            let index = state.cart.findIndex(function(item){
                if(
                    item.id === action.data.id &&
                    item.size === action.data.size &&
                    item.color === action.data.color
                ){
                    return true;
                }
            });

            if(index === -1){
                state.cart.push(action.data);
            }else{
                state.cart[index].total += action.data.total;
            }

            return {...state};
        }

        case 'EDIT_CART' : {
            let index = state.cart.findIndex(function(item){
                if(
                    item.id === action.data.id &&
                    item.size === action.data.size &&
                    item.color === action.data.color
                ){
                    return true;
                }
            });
            if(index !== -1){
                state.cart[index].total = action.data.total;
            }
            return {...state};
        }

        case 'DELETE_CART_ITEM' : {
            let index = state.cart.findIndex(function(item){
                return item.id === action.data.id;
            });
            if(index !== -1){
                state.cart.splice(index, 1);
            }
            return {...state};
        }

        case 'DESTROY_CART' : {
            state.cart = [];
            return {...state};
        }

        default:
            break;
    }
    return { ...state };
}

export default BaiTapGioHangReducer;