import React, { useState, useEffect } from 'react';
import BreadCrumb from '../../Components/BreadCrumb/BreadCrumb';
import st from './ProductCategory.module.css';
import Slider from '@mui/material/Slider';
import Pagination from '@mui/material/Pagination';
import ProductCard from '../../Components/ProductCard/ProductCard';
import axios from 'axios';
import { useParams, Link } from 'react-router-dom';
import { baseApiUrl, baseImageUrl, baseUrl } from '../../Constants/UrlConstant';
import { productCategory } from '../../Constants/PaginateConstant';
import { priceRangeConstant } from '../../Constants/PriceRangeConstant';
import { Helmet } from "react-helmet";

export default function ProductCategory() {

    let params = useParams();
    let [priceRange, setPriceRange] = useState([priceRangeConstant[0], priceRangeConstant[1]]);
    let [activeOrderBy, setActiveOrderBy] = useState(1);
    let [products, setProducts] = useState();
    let [categories, setCategories] = useState([]);
    let [detailCategory, setDetailCategory] = useState();
    let [sizes, setSizes] = useState([]);
    let [colors, setColors] = useState([]);
    let [filterUrl, setFilterUrl] = useState({
        color_id: [],
        category_url: params.category_url === undefined ? [] : [params.category_url],
        size_id: [],
        price_range: [priceRangeConstant[0], priceRangeConstant[1]],
        order_by: 'id|desc'
    });
    let [page, setPage] = useState(1);
    let [changeFilter, setChangeFilter] = useState("");

    useEffect(function () {
        if(params.category_url){
            axios.get(`${baseApiUrl}/product-category/${params.category_url}`)
            .then(function(res){
                setDetailCategory(res.data.data);
            });
        }
        let newFilterUrl = { ...filterUrl };
        newFilterUrl.category_url = [params.category_url];
        setFilterUrl(newFilterUrl);
        setChangeFilter(getCurrentTime());
    }, [params.category_url]);

    useEffect(function () {
        axios.get(`${baseApiUrl}/all-category`)
            .then(function (res) {
                setCategories(res.data.data);
            });

        axios.get(`${baseApiUrl}/sizes`)
            .then(function (res) {
                setSizes(res.data.data);
            });

        axios.get(`${baseApiUrl}/colors`)
            .then(function (res) {
                setColors(res.data.data);
            });
    }, []);

    useEffect(function () {
        let keyword = params.keyword === undefined ? "all" : params.keyword;
        axios.get(`${baseApiUrl}/get-products?limit=${productCategory}&page=${page}&color_id=${filterUrl.color_id}&size_id=${filterUrl.size_id}&price_range=${filterUrl.price_range}&order_by=${filterUrl.order_by}&category_url=${filterUrl.category_url}&keyword=${keyword === "all" ? "" : keyword}`)
            .then(function (res) {
                setProducts(res.data.data);
            });

    }, [page, filterUrl.order_by, changeFilter, params.keyword]);

    function getCurrentTime() {
        var today = new Date();
        var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
        var time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
        var dateTime = date + ' ' + time;
        return dateTime;
    }

    function renderCategories() {
        return categories?.map(function (value, index) {
            return (
                <div
                    key={index}
                    className={st.category_input_item}
                >
                    <input
                        id={value.id}
                        type="checkbox"
                        onClick={(e) => {
                            let newFilterUrl = { ...filterUrl };
                            let index = newFilterUrl.category_url.findIndex(function (item) {
                                return item === value.url;
                            });
                            if (e.target.checked) {
                                newFilterUrl.category_url.push(value.url);
                            } else {
                                newFilterUrl.category_url.splice(index, 1);
                            }
                            setFilterUrl(newFilterUrl);
                        }}
                    />
                    <label htmlFor={value.id}>{value.name}</label>
                </div>
            );
        });
    }

    function renderProduct() {
        return products?.data.map(function (value, index) {
            return <Link to={`/product/${value.url}.html`} key={index}><ProductCard data={value} /></Link>
        });
    }

    function renderSizes() {
        return sizes?.map(function (value, index) {
            return (
                <div key={index} className={st.category_input_item}>
                    <input
                        id={`size-${value.id}`}
                        type="checkbox"
                        onClick={(e) => {
                            let newFilterUrl = { ...filterUrl };
                            let index = newFilterUrl.size_id.findIndex(function (item) {
                                return item === value.id;
                            });
                            if (e.target.checked) {
                                newFilterUrl.size_id.push(value.id);
                            } else {
                                newFilterUrl.size_id.splice(index, 1);
                            }
                            setFilterUrl(newFilterUrl);
                        }}
                    />
                    <label htmlFor={`size-${value.id}`}>Size {value.name}</label>
                </div>
            )
        })
    }

    function showActiveColor(id) {
        let index = filterUrl.color_id.findIndex(function (item) {
            return item === id;
        });
        return (index !== -1) ? true : false;
    }

    function renderColors() {
        return colors?.map(function (value, index) {
            return (
                <li
                    key={index}
                    style={{ background: value.color_code }}
                    className={`${st.color_item} ${showActiveColor(value.id) ? st.color_item_active : ""}`}
                    onClick={() => {
                        let newFilterUrl = { ...filterUrl };
                        if (newFilterUrl.color_id.length === 0) {
                            newFilterUrl.color_id.push(value.id);
                        }
                        else {
                            let index = newFilterUrl.color_id.findIndex(function (item) {
                                return item === value.id;
                            });
                            if (index === -1) {
                                newFilterUrl.color_id.push(value.id);
                            } else {
                                newFilterUrl.color_id.splice(index, 1);
                            }
                        }
                        setFilterUrl(newFilterUrl);
                    }}
                >
                    <div></div>
                </li>
            );
        });
    }

    function renderPaginateButton() {
        let siblingCount = 2;
        let boundaryCount = 1;
        if (window.screen.width < 426) {
            siblingCount = 1;
        }
        if (window.screen.width < 376) {
            siblingCount = 1;
            boundaryCount = 0;
        }
        return (<Pagination
            count={Math.ceil(products?.total / productCategory)}
            boundaryCount={boundaryCount}
            siblingCount={siblingCount}
            page={page}
            onChange={(event, value) => {
                window.scrollTo(0, 0);
                setPage(value);
            }}
        />);
    }

    return (
        <div className={st.wrap_product_category}>
            <Helmet>
                <title>Fire Sport Shop - {`${detailCategory ? detailCategory.name : "Danh mục sản phẩm"}`}</title>
                <meta property="og:title" content={`Fire Sport Shop - ${detailCategory ? detailCategory.name : "Danh mục sản phẩm"}`} />
                <meta name="description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
                <meta property="og:description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
                <meta
                    property="og:image"
                    content={`${baseImageUrl}/${detailCategory ? `productCategories/${detailCategory.big_image}` : "base/logo.png"}`}
                />
                <meta property="og:image:alt"
                    content={`${baseImageUrl}/${detailCategory ? `productCategories/${detailCategory.big_image}` : "base/logo.png"}`}
                />
                <meta property="og:site_name" content={baseUrl} />
                <meta property="og:url" content={`${baseUrl}/product-category/category/${params.category_url}.html`} />
            </Helmet>
            <BreadCrumb></BreadCrumb>
            <div className={`container ${st.main_product_category}`}>
                <div className="row">
                    <div className={`col-lg-3 ${st.col_left}`}>
                        <h2 className={st.title_filter}>Phân loại sản phẩm</h2>
                        <div className={st.by_category}>
                            <b className={st.filter_title_item}>Loại sản phẩm</b>
                            {renderCategories()}
                        </div>
                        <div className={st.by_color}>
                            <b className={st.filter_title_item}>Màu sắc</b>
                            <ul className={st.wrap_color_item_filter}>
                                {renderColors()}
                            </ul>
                        </div>
                        <div className={st.by_size}>
                            <b className={st.filter_title_item}>Theo size</b>
                            {renderSizes()}
                        </div>
                        <div className={st.by_price}>
                            <b className={st.filter_title_item}>Khoảng giá</b>
                            <div className={st.wrap_filter_price}>
                                <input
                                    value={`${priceRange[0].toLocaleString()} đ`}
                                    type="text"
                                    name="0"
                                    onChange={(e) => {
                                        let newPriceRange = [...priceRange];
                                        newPriceRange[0] = e.target.value;
                                        setPriceRange(newPriceRange);
                                    }}
                                />
                                <span>-</span>
                                <input
                                    value={`${priceRange[1].toLocaleString()} đ`}
                                    type="text"
                                />
                            </div>
                            <Slider
                                getAriaLabel={() => 'Minimum distance'}
                                // value=
                                onChange={function (event, value) {
                                    let newPriceRange = [...priceRange];
                                    let newFilterUrl = { ...filterUrl };
                                    newPriceRange = value;
                                    newFilterUrl.price_range = value;
                                    setFilterUrl(newFilterUrl);
                                    setPriceRange(newPriceRange);
                                }}
                                valueLabelDisplay="auto"
                                // getAriaValueText={valuetext}
                                disableSwap
                                defaultValue={[priceRangeConstant[0], priceRangeConstant[1]]}
                                min={priceRangeConstant[0]}
                                max={priceRangeConstant[1]}
                                step={50000}
                                sx={{
                                    color: 'black',
                                    zIndex: 1,
                                    userSelect: 'none',
                                    '& .MuiSlider-thumb': {
                                        width: '15px',
                                        height: '15px'
                                    },
                                }}
                            />
                        </div>
                        <div className="text-center">
                            <div
                                className={st.filter_button}
                                onClick={() => {
                                    window.scrollTo(0, 0);
                                    setPage(1);
                                    setChangeFilter(getCurrentTime());
                                    console.log(filterUrl);
                                }}
                            >Lọc sản phẩm</div>
                        </div>
                    </div>
                    <div className={`col-lg-9 ${st.col_right}`}>
                        <div className={st.wrap_order_by}>
                            <div className={st.title_order_by}><b>Sắp xếp</b></div>
                            <ul className={st.order_by_group}>
                                <li
                                    className={`${st.order_by_item} ${activeOrderBy === 1 ? st.order_by_item_active : ""}`}
                                    onClick={() => {
                                        setActiveOrderBy(1);
                                        let newFilterUrl = { ...filterUrl };
                                        newFilterUrl.order_by = "id|desc";
                                        setFilterUrl(newFilterUrl);
                                    }}
                                >
                                    <div>Mặc định</div>
                                </li>
                                <li
                                    className={`${st.order_by_item} ${activeOrderBy === 2 ? st.order_by_item_active : ""}`}
                                    onClick={() => {
                                        setActiveOrderBy(2);
                                        let newFilterUrl = { ...filterUrl };
                                        newFilterUrl.order_by = "name|asc";
                                        setFilterUrl(newFilterUrl);
                                    }}
                                >
                                    <div>A <i className="fa-solid fa-arrow-right-long"></i> Z</div>
                                </li>
                                <li
                                    className={`${st.order_by_item} ${activeOrderBy === 3 ? st.order_by_item_active : ""}`}
                                    onClick={() => {
                                        setActiveOrderBy(3);
                                        let newFilterUrl = { ...filterUrl };
                                        newFilterUrl.order_by = "name|desc";
                                        setFilterUrl(newFilterUrl);
                                    }}
                                >
                                    <div>Z <i className="fa-solid fa-arrow-right-long"></i> A</div>
                                </li>
                                <li
                                    className={`${st.order_by_item} ${activeOrderBy === 4 ? st.order_by_item_active : ""}`}
                                    onClick={() => {
                                        setActiveOrderBy(4);
                                        let newFilterUrl = { ...filterUrl };
                                        newFilterUrl.order_by = "price_sale|asc";
                                        setFilterUrl(newFilterUrl);
                                    }}
                                >
                                    <div>Giá tăng dần</div>
                                </li>
                                <li
                                    className={`${st.order_by_item} ${activeOrderBy === 5 ? st.order_by_item_active : ""}`}
                                    onClick={() => {
                                        setActiveOrderBy(5);
                                        let newFilterUrl = { ...filterUrl };
                                        newFilterUrl.order_by = "price_sale|desc";
                                        setFilterUrl(newFilterUrl);
                                    }}
                                >
                                    <div>Giá giảm dần</div>
                                </li>
                                <li
                                    className={`${st.order_by_item} ${activeOrderBy === 6 ? st.order_by_item_active : ""}`}
                                    onClick={() => {
                                        setActiveOrderBy(6);
                                        let newFilterUrl = { ...filterUrl };
                                        newFilterUrl.order_by = "created_at|desc";
                                        setFilterUrl(newFilterUrl);
                                    }}
                                >
                                    <div>Mới nhất</div>
                                </li>
                                <li
                                    className={`${st.order_by_item} ${activeOrderBy === 7 ? st.order_by_item_active : ""}`}
                                    onClick={() => {
                                        setActiveOrderBy(7);
                                        let newFilterUrl = { ...filterUrl };
                                        newFilterUrl.order_by = "created_at|asc";
                                        setFilterUrl(newFilterUrl);
                                    }}
                                >
                                    <div>Cũ nhất</div>
                                </li>
                            </ul>
                        </div>
                        {
                            products?.data.length === 0
                                ? <div className='alert alert-warning'>Không có sản phẩm</div>
                                : ""
                        }
                        <div className={st.wrap_product}>
                            {renderProduct()}
                        </div>
                        <div className={st.wrap_paginate}>
                            {renderPaginateButton()}
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}
