import React from 'react';
import { useEffect } from 'react';
import BlogCard from '../../Components/BlogCard/BlogCard';
import Category from '../../Components/Category/Category';
import ProductCard from '../../Components/ProductCard/ProductCard';
import Slider from '../../Components/Slider/Slider';
import Title from '../../Components/Title/Title';
import st from './Home.module.css';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { useState } from 'react';
import { baseApiUrl, baseImageUrl, baseUrl } from '../../Constants/UrlConstant';
import { Helmet } from "react-helmet";

export default function Home() {
  let [productNewArrival, setProductNewArrival] = useState([]);
  let [accessory, setAccessory] = useState([]);
  let [blogs, setBlogs] = useState([]);
  let [sliders, setSliders] = useState([]);

  useEffect(function () {
    axios.get(`${baseApiUrl}/sliders`)
      .then(function (res) {
          setSliders(res.data.data);
      });
    axios.get(`${baseApiUrl}/get-products?products.order_by=id|desc&limit=8`)
      .then(function (res) {
        setProductNewArrival(res.data.data.data);
      });
    axios.get(`${baseApiUrl}/get-products?products.order_by=id|desc&limit=8&category_url=phu-kien`)
      .then(function (res) {
        setAccessory(res.data.data.data);
      });
    axios.get(`${baseApiUrl}/get-blogs?limit=4`)
      .then(function (res) {
        setBlogs(res.data.data);
      });
  }, []);

  function renderNewArrival() {
    return productNewArrival?.map(function (value, index) {
      return (
        <Link
          key={index}
          to={`/product/${value.url}.html`}
          onClick={() => { window.scrollTo(0, 0) }}
        >
          <ProductCard data={value} />
        </Link>
      );
    });
  }

  function renderAccessory() {
    return accessory?.map(function (value, index) {
      return (
        <Link
          key={index}
          to={`/product/${value.url}.html`}
          onClick={() => { window.scrollTo(0, 0) }}
        >
          <ProductCard data={value} />
        </Link>
      );
    })
  }

  function renderBlogs() {
    return blogs?.map(function (value, index) {
      return <Link
        to={`/blog/${value.url}.html`}
        onClick={() => {
          window.scrollTo(0, 0);
        }}
      >
        <BlogCard key={index} data={value}></BlogCard>
      </Link>;
    });
  }

  return (
    <>
      <Helmet>
        <title>Fire Sport Shop - Trang Chủ</title>
        <meta property="og:title" content="Fire Sport Shop" />
        <meta name="description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
        <meta property="og:description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
        <meta
          property="og:image"
          content={`${baseImageUrl}/sliders/${sliders[0]?.big_image}`}
        />
        <meta property="og:image:alt" content={`${baseImageUrl}/sliders/${sliders[0]?.big_image}`} />
        <meta property="og:site_name" content={baseUrl}/>
        <meta property="og:url" content={baseUrl}/>
      </Helmet>
      <div className={st.wrap_slider}>
        <Slider sliders={sliders}></Slider>
      </div>
      <div className="container">
        <Title title="New Arrival" />
        <div className={st.wrap_new_arrival}>
          {renderNewArrival()}
        </div>
        <div className={st.wrap_category}>
          <Title title="Category"></Title>
          <Category></Category>
        </div>
        <Title title="Phụ kiện"></Title>
        <div className={st.wrap_other}>
          {renderAccessory()}
        </div>
        <Title title="Tin tức thời trang"></Title>
        <div className={st.wrap_blog_card}>
          {renderBlogs()}
        </div>
      </div>
    </>
  )
}
