import React, { useState } from 'react';
import BreadCrumb from '../../Components/BreadCrumb/BreadCrumb';
import InputTotal from '../../Components/InputTotal/InputTotal';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import st from './Cart.module.css';
import { baseImageUrl, baseUrl } from '../../Constants/UrlConstant';
import { Helmet } from "react-helmet";

function Cart(props) {
    function totalOrder() {
        return props.cart?.cart.reduce(function (count, item) {
            return count += item.price * item.total;
        }, 0);
    }

    function renderProducts() {
        return props.cart?.cart.map(function (value, index) {
            return (
                <tr key={index}>
                    <td>
                        <Link to={`/product/${value.url}.html`}>
                        <div className={st.wrap_product_img}>
                            <img
                                className={st.product_img}
                                src={`${baseImageUrl}/products/${value.image}`}
                                alt=""
                            />
                        </div>
                        </Link>
                    </td>
                    <td>
                        <Link to={`/product/${value.url}.html`}><div>{value.name}</div></Link>
                        <div>
                            <span>Size {value.sizeName}</span> -
                            <span style={{ backgroundColor: value.colorName }} className={st.color_item}></span>
                        </div>
                    </td>
                    <td>{value.price.toLocaleString()}đ</td>
                    <td>
                        <InputTotal
                            totalProduct={value.total}
                            total={function (total) {
                                props.editCartItem({
                                    id: value.id,
                                    color: value.color,
                                    size: value.size,
                                    total: total
                                });
                            }}
                        >
                        </InputTotal>
                    </td>
                    <td>{(value.price * value.total).toLocaleString()}đ</td>
                    <td>
                        <button
                            className={st.remove_item_button}
                            onClick={() => {
                                props.deleteCartItem(value);
                            }}
                        ><i class="fa-solid fa-trash-can"></i></button>
                    </td>
                </tr>
            );
        });
    }

    return (
        <div className={st.cart_page}>
            <Helmet>
                <title>Fire Sport Shop - Giỏ hàng</title>
                <meta property="og:title" content="Fire Sport Shop - Giỏ hàng" />
                <meta name="description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
                <meta property="og:description" content="Firesport shop cung cấp các sản phẩm thời trang thể thao" />
                <meta
                    property="og:image"
                    content=""
                />
                <meta property="og:image:alt"
                    content=""
                />
                <meta property="og:site_name" content={baseUrl} />
                <meta property="og:url" content={`${baseUrl}/cart.html`} />
            </Helmet>
            <BreadCrumb></BreadCrumb>
            <div className={`container ${st.container_cart_page}`}>
                <div className={st.title_cart_page}>Giỏ hàng của bạn</div>
                <table className={`${st.cart_table_desk_top}`}>
                    <thead>
                        <tr>
                            <th>Ảnh sản phẩm</th>
                            <th>Tên sản phẩm</th>
                            <th>Đơn giá</th>
                            <th>Số lượng</th>
                            <th>Thành tiền</th>
                            <th>Xóa</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.cart.cart.length === 0 ? <tr className='alert'>
                            <td colSpan={6}>Giỏ hàng rỗng !</td>
                        </tr> : ""}
                        {renderProducts()}
                    </tbody>
                </table>
                <div className={st.wrap_mobile_cart}>
                    <div className={st.item}>
                        <div className={st.wrap_mobile_img}>
                            <img src="https://bizweb.sapocdn.net/100/395/283/products/ec01c2fd75e6b7b8eef76.jpg?v=1658548614697" alt="" />
                        </div>
                        <div className={st.mobile_info}>
                            <div className={st.name}>FAPAS EIGHT-BALL TEE-XANH / S</div>
                            <div className={st.price}><span>Giá: </span> <b>320.000đ</b></div>
                        </div>
                        <div className={st.mobile_total}>
                            <InputTotal total={function (total) { }}></InputTotal>
                            <button className={st.mobile_remove}>Xóa</button>
                        </div>
                    </div>
                    <div className={st.item}>
                        <div className={st.wrap_mobile_img}>
                            <img src="https://bizweb.sapocdn.net/100/395/283/products/ec01c2fd75e6b7b8eef76.jpg?v=1658548614697" alt="" />
                        </div>
                        <div className={st.mobile_info}>
                            <div className={st.name}>FAPAS EIGHT-BALL TEE-XANH / S</div>
                            <div className={st.price}><span>Giá: </span> <b>320.000đ</b></div>
                        </div>
                        <div className={st.mobile_total}>
                            <InputTotal total={function (total) { }}></InputTotal>
                            <button className={st.mobile_remove}>Xóa</button>
                        </div>
                    </div>
                </div>
                <div className={st.wrap_total}>
                    <Link to="/"><button className={st.continute}>TIẾP TỤC MUA HÀNG</button></Link>
                    <table className={st.main_total}>
                        <td>Tổng tiền thanh toán</td>
                        <td>{totalOrder().toLocaleString()}đ</td>
                    </table>
                </div>
                <div className={st.wrap_button_pay}>
                    {
                        props.cart?.cart.length !== 0
                        ? (
                            <Link to="/checkout.html">
                                <button>Tiến hành thanh toán</button>
                            </Link>
                        )
                        : (
                            <button className={st.disabled}>Tiến hành thanh toán</button>
                        )
                    }
                </div>
            </div>
        </div>
    )
}

function mapStateToProps(state) {
    return state;
}

function mapDispatchToProps(dispatch) {
    return {
        editCartItem: function (product) {
            let action = {
                type: "EDIT_CART",
                data: {
                    id: product.id,
                    total: product.total,
                    color: product.color,
                    size: product.size
                }
            }
            dispatch(action);
        },
        deleteCartItem: function (product) {
            let action = {
                type: "DELETE_CART_ITEM",
                data: {
                    id: product.id
                }
            }
            console.log(action);
            dispatch(action);
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Cart);