import React, { useEffect, useState } from 'react';
import { baseImageUrl } from '../../Constants/UrlConstant';
import st from './MobileProductCart.module.css';
import { connect } from 'react-redux';

function MobileProductCart(props) {

    let [total, setTotal] = useState(props.totalProduct);

    useEffect(function () {
        props.total(total);
    }, [total]);

    console.log(props.value);

    return (
        <div className={`${st.mobile_product_cart}`}>
            <div className={st.left}>
                <div className={st.wrap_img}>
                    <img
                        src={`${baseImageUrl}/products/${props.value.image}`}
                        alt={props.value.image}
                    />
                </div>
            </div>
            <div className={`${st.center}`}>
                <div className={st.name}>{props.value.name}</div>
                <div>
                    <span
                        style={{background: props.value.colorName}}
                        className={st.color_box}>
                    </span>
                    <span className={st.size_box}>Size: S</span>
                </div>
                <div className={st.total}>
                    <button
                        onClick={() => {
                            let newTotal = total - 1;
                            if (newTotal < 1) {
                                newTotal = 1;
                            }
                            setTotal(parseInt(newTotal));
                        }}
                    >-</button>
                    <input
                        type="text"
                        value={total}
                        onChange={
                            (e) => {
                                let newTotal = 1;
                                if (
                                    e.target.value < 1 ||
                                    isNaN(e.target.value)
                                ) {
                                    newTotal = total;
                                } else {
                                    newTotal = e.target.value;
                                }
                                setTotal(parseInt(newTotal));
                            }
                        }
                    />
                    <button
                        onClick={() => {
                            setTotal(parseInt(total) + 1);
                        }}
                    >+</button>
                </div>
            </div>
            <div className={st.right}>
                <button
                    className={st.remove}
                    onClick={() => {
                        props.deleteCartItem(props.value);
                    }}
                >x</button>
            </div>
        </div>
    )
}

function mapDispatchToProps(dispatch){
    return {
        deleteCartItem: function (product) {
            let action = {
                type: "DELETE_CART_ITEM",
                data: {
                    id: product.id
                }
            }
            console.log(action);
            dispatch(action);
        }
    }
}

export default connect(null, mapDispatchToProps)(MobileProductCart)