import React from 'react';
import st from './BlogDetail.module.css';
import BreadCrumb from '../../Components/BreadCrumb/BreadCrumb';
import BlogCategoryCards from '../../Components/BlogCategoryCards/BlogCategoryCards';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { useEffect } from 'react';
import { baseApiUrl } from '../../Constants/UrlConstant';
import { useState } from 'react';
import Highlight from 'react-highlight';

export default function BlogDetail() {

    let params = useParams();
    let [blogDetail, setBlogDetail] = useState();

    useEffect(function () {
        axios.get(`${baseApiUrl}/blog/${params.url}`)
            .then(function (res) {
                setBlogDetail(res.data.data);
            })
    }, [params.url]);

    return (
        <div>
            <BreadCrumb></BreadCrumb>
            <div className={`container ${st.wrap_all_container}`}>
                <div className="row">
                    <div className="col-lg-8">
                        <div className="wrap_all">
                            <div className="head">
                                <div className={st.title}>{blogDetail?.name}</div>
                                <div className={st.wrap_author}>
                                    <div className={st.item_author}>
                                        <i class="fa-solid fa-calendar-days"></i>
                                        <span>{blogDetail?.create_at}</span>
                                    </div>
                                    <div className={st.item_author}>
                                        <i class="fa-solid fa-user"></i>
                                        <span>{blogDetail?.user.name}</span>
                                    </div>
                                </div>
                            </div>
                            <div className={st.wrap_all_content}>
                                <div className="main_content">
                                    <Highlight innerHTML={true}>
                                        {blogDetail?.long_desc}
                                    </Highlight>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        <BlogCategoryCards
                            title="Tin mới nhất"
                            url={`${baseApiUrl}/get-blogs?order_by=id|desc`}
                        />
                        <BlogCategoryCards
                            title="Tin hot nhất"
                            url={`${baseApiUrl}/get-blogs?order_by=id|desc&special=true`}
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}
