import React, { useEffect, useState } from 'react';
import ProductCard from '../../Components/ProductCard/ProductCard';
import BreadCrumb from '../../Components/BreadCrumb/BreadCrumb';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation, Lazy } from "swiper";
import Magnifier from "react-magnifier";
import 'swiper/css';
import 'swiper/css/autoplay';
import 'swiper/css/lazy';
import 'swiper/css/navigation';
import Button from '@mui/material/Button';
import st from './ProductDetail.module.css';
import axios from 'axios';
import { connect } from 'react-redux';
import { useAlert } from 'react-alert';
import { baseApiUrl, baseImageUrl, baseUrl } from '../../Constants/UrlConstant';
import { Link, useParams } from 'react-router-dom';
import { productDetail } from '../../Constants/PaginateConstant';
import { Helmet } from "react-helmet";

function ProductDetail(props) {

    let params = useParams();
    const alert = useAlert();
    let [bigImgUrl, setBigImgUrl] = useState("");
    let [productData, setProductData] = useState();
    let [selectData, setSelectData] = useState({
        color: null,
        colorName: "",
        size: null,
        sizeName: "",
        total: 1,
    });
    let [otherProduct, setOtherProduct] = useState([]);
    let [urlOtherProduct, setUrlOtherProduct] = useState({
        current: 1,
        url: ""
    });

    useEffect(function () {
        axios.get(`${baseApiUrl}/product/${params.url}`)
            .then(function (res) {
                setProductData(res.data.data);
                setBigImgUrl(`${baseImageUrl}/products/${res.data.data.image_1}`);
                let newSelectData = { ...selectData };
                newSelectData.size = res.data.data.sizes[0].id;
                newSelectData.color = res.data.data.colors[0].id;
                newSelectData.colorName = res.data.data.colors[0].color_code;
                newSelectData.sizeName = res.data.data.sizes[0].name;
                setSelectData(newSelectData);
                setUrlOtherProduct({
                    current: 1,
                    url: `${baseApiUrl}/get-products?limit=${productDetail}&other_current_url=${params.url}&category_url=${res.data.data.category.url}`
                });
            });
    }, [params.url]);

    useEffect(function () {
        axios.get(urlOtherProduct.url)
            .then(function (res) {
                setOtherProduct(res.data.data.data);
            });
    }, [urlOtherProduct]);

    function renderProductImages() {
        return productData?.product_images.map(function (value, index) {
            return (
                <SwiperSlide key={index} onClick={() => {
                    setBigImgUrl(`${baseImageUrl}/productImages/${value.big_image}`);
                }} className={st.wrap_thumb_item}>
                    <img className={st.thumb_item} src={`${baseImageUrl}/productImages/${value.big_image}`} alt="" />
                </SwiperSlide>
            );
        });
    }

    function renderProductColors() {
        return productData?.colors.map(function (value, index) {
            return (
                <li key={index}>
                    <Button
                        style={{ background: value.color_code }}
                        className={`${st.color_item_select} ${value.id === selectData.color ? st.color_item_select_active : ""}`}
                        onClick={() => {
                            let newSelectData = { ...selectData };
                            newSelectData.color = value.id;
                            newSelectData.colorName = value.color_code;
                            setSelectData(newSelectData);
                        }}
                    ></Button>
                </li>
            );
        })
    }

    function renderProductSizes() {
        return productData?.sizes.map(function (value, index) {
            return (
                <li key={index}>
                    <Button
                        onClick={() => {
                            let newSelectData = { ...selectData };
                            newSelectData.size = value.id;
                            newSelectData.sizeName = value.name;
                            setSelectData(newSelectData);
                        }}
                        className={`${st.size_select_item} ${value.id === selectData.size ? st.size_select_item_active : ""}`}
                    >{value.name}</Button>
                </li>
            );
        })
    }

    function renderOtherProduct() {
        return otherProduct?.map(function (value, index) {
            return (
                <Link
                    key={index}
                    to={`/product/${value.url}.html`}
                    onClick={() => {
                        window.scrollTo(0, 0);
                    }}
                >
                    <ProductCard data={value} />
                </Link>
            );
        });
    }

    return (
        <div className='wrap_product_detail'>
            <Helmet>
                <title>Fire Sport Shop - {productData !== undefined ? productData.name : ""}</title>
                <meta
                    property="og:title"
                    content={`Fire Sport Shop - ${productData !== undefined ? productData.name : ""}`}
                />
                <meta
                    name="description"
                    content={productData !== undefined ? productData.short_desc : ""}
                />
                <meta
                    property="og:description"
                    content={productData !== undefined ? productData.short_desc : ""}
                />
                <meta
                    property="og:image"
                    content={`${baseImageUrl}/products/${productData ? productData.image_1 : ""}`}
                />
                <meta property="og:image:alt"
                    content={`${baseImageUrl}/products/${productData ? productData.image_1 : ""}`}
                />
                <meta property="og:site_name" content={baseUrl} />
                <meta property="og:url" content={`${baseUrl}/product/${productData ? productData.url : ""}.html`} />
            </Helmet>
            <BreadCrumb></BreadCrumb>
            <div className="container">
                <div className={`row ${st.row_product_detail}`}>
                    <div className="col-lg-6">
                        <div className="wrap_preview_img">
                            <div className={st.big_img}>
                                <Magnifier mgWidth={200} mgHeight={200} width={`100%`} height={`100%`} className={st.big_img_main} src={bigImgUrl ? bigImgUrl : ""} />
                            </div>
                            <div className="thumb_img">
                                <Swiper
                                    style={{
                                        "swiper-button-prev": '15px',
                                    }}
                                    spaceBetween={10}
                                    navigation={{ clickable: true }}
                                    breakpoints={{
                                        // when window width is >= 270px
                                        270: {
                                            slidesPerView: 2,
                                            spaceBetween: 5
                                        },
                                        // when window width is >= 425
                                        425: {
                                            slidesPerView: 3,
                                            spaceBetween: 5
                                        },
                                        // when window width is >= 580
                                        580: {
                                            slidesPerView: 4,
                                            spaceBetween: 10
                                        }
                                    }}
                                    loop={false}
                                    autoplay={{
                                        delay: 10000,
                                        disableOnInteraction: false,
                                    }}
                                    lazy={true}
                                    modules={[Autoplay, Navigation, Lazy]}
                                >
                                    {renderProductImages()}
                                </Swiper>
                            </div>
                        </div>
                    </div>
                    <div className={`col-lg-6 ${st.col_right}`}>
                        <div className={st.name}>{productData?.name}</div>
                        <div>
                            <span className={st.main_price}>{productData ? (productData.price_sale).toLocaleString() : "Loading"}₫</span>
                            <del className={st.del_price}>{productData ? (productData.price_root).toLocaleString() : "Loading"}₫</del>
                        </div>
                        <hr />
                        <div className="short_desc">
                            {productData?.short_desc}
                        </div>
                        <hr />
                        <div className={st.wrap_select_type}>
                            <span className={st.wrap_select_type_title}>Màu sắc:</span>
                            <ul className={st.wrap_select_img}>
                                {renderProductColors()}
                            </ul>
                        </div>
                        <div className={st.wrap_select_type}>
                            <span className={st.wrap_select_type_title}>Kích cỡ:</span>
                            <ul className={st.wrap_size}>
                                {renderProductSizes()}
                            </ul>
                        </div>
                        <div className={st.wrap_select_type}>
                            <span className={st.wrap_select_type_title}>Số lượng:</span>
                            <div className="d-flex flex-wrap">
                                <div className={st.wrap_select_total}>
                                    <button
                                        onClick={() => {
                                            let newSelectData = { ...selectData };
                                            if (selectData.total > 1) {
                                                newSelectData.total -= 1;
                                            }
                                            setSelectData(newSelectData);
                                        }}
                                    >-</button>
                                    <input
                                        value={selectData.total}
                                        type="text"
                                        onChange={(e) => {
                                            let newSelectData = { ...selectData };
                                            let value = e.target.value;
                                            if (
                                                isNaN(value) ||
                                                parseInt(value) < 1
                                            ) {
                                                newSelectData.total = parseInt(selectData.total);
                                            } else {
                                                newSelectData.total = parseInt(value);
                                            }
                                            setSelectData(newSelectData);
                                        }}
                                    />
                                    <button
                                        onClick={() => {
                                            let newSelectData = { ...selectData };
                                            newSelectData.total += 1;
                                            setSelectData(newSelectData);
                                        }}
                                    >+</button>
                                </div>
                                <div className={st.button_size_tutorial}>
                                    <div className={st.size_tutorial}>
                                        <img src="./img/ruler.webp" alt="ruler" />
                                        <span>Hướng dẫn đo size</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <hr className={st.hr_total} />
                        <div className={`${st.buy_button} ${st.add_to_cart_button}`}>
                            <div
                                onClick={() => {
                                    props.addToCart(productData, selectData);
                                    alert.show('Thêm vào giỏ hàng thành công !', {
                                        type: 'success',
                                    })
                                }}
                            >
                                <i class="fa-solid fa-plus"></i>
                                <span>Thêm vào giỏ</span>
                            </div>
                        </div>
                        <div className={`${st.buy_button} ${st.buy_now_button}`}>
                            <div>
                                <i class="fa-solid fa-money-check"></i>
                                <span>Mua ngay</span>
                            </div>
                        </div>
                        <div className={`${st.buy_button} ${st.cal_now_button}`}>
                            <div>
                                <i class="fa-solid fa-phone"></i>
                                <span>Gọi ngay</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={st.wrap_other}>
                    <div className={st.wrap_tab}>
                        <div
                            className={urlOtherProduct.current === 1 ? st.active : ""}
                            onClick={() => {
                                setUrlOtherProduct({
                                    current: 1,
                                    url: `${baseApiUrl}/get-products?limit=${productDetail}&other_current_url=${params.url}&category_url=${productData.category.url}`
                                });
                            }}
                        >SẢN PHẨM CÙNG LOẠI</div>
                        <div
                            className={urlOtherProduct.current === 2 ? st.active : ""}
                            onClick={() => {
                                setUrlOtherProduct({
                                    current: 2,
                                    url: `${baseApiUrl}/get-products?limit=${productDetail}&other_current_url=${params.url}&order_by=id|asc`
                                });
                            }}
                        >SẢN PHẨM MỚI</div>
                    </div>
                    <div className={st.wrap_other_product}>
                        {renderOtherProduct()}
                    </div>
                </div>
            </div>
        </div>
    )
}


function mapStateToProps(state) {
    return {
        cart: state.cart
    }
}

function mapDispatchToProps(dispatch) {
    return {
        addToCart: function (productData, selectData) {
            let action = {
                type: "ADD_TO_CART",
                data: {
                    id: productData.id,
                    url: productData.url,
                    name: productData.name,
                    image: productData.image_1,
                    price: productData.price_sale,
                    color: selectData.color,
                    colorName: selectData.colorName,
                    size: selectData.size,
                    sizeName: selectData.sizeName,
                    total: selectData.total
                }
            }
            dispatch(action);
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductDetail)